.Persik {
    margin:auto auto auto auto;
    height: 70vh; /* высота картинки */
    display:flex;
    align-items: center;
    justify-content: center;
    margin-top: 5vmin;

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;

}



.noselect {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}


.menu img {
    width: 60px;
    position: fixed;
    bottom:10px;
    right: 10px;
    height: 60px;

    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.imgret {
    background: #fff; /* Цвет фона */
    color: #fff; /* Цвет текста */
    padding: 0px; /* Поля вокруг текста */
    min-height: 18%;
    min-width: 18%;

    border-radius: 20px; /* Уголки */
}
.newgroup {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;

    box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);

    border-radius: 20px; /* Уголки */
}
.newgroup-button-light {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    background-color:white;
    box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;

    border-radius: 20px; /* Уголки */
}
.newgroup-button-light-pressed {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    background:#cccccc;
    box-shadow:0px 4px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 4px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 4px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;

    border-radius: 20px; /* Уголки */
}

.newgroup-button-dark {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    background-color:#19191a;
    box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;

    border-radius: 20px; /* Уголки */
}
.newgroup-button-dark-pressed {

    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    background:#2a2a2c;
    box-shadow:0px 3px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 3px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 3px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;

    border-radius: 20px; /* Уголки */
}


.overlay {

    overflow:visible;
    pointer-events:none;

}
.newgroup-button:active {
    background:#cccccc;
    box-shadow:0px 4px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 4px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 4px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;
}
.newgroup-button-dark {
    padding: 0px; /* Поля вокруг текста */
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    background: #19191a;
    box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 1px 6px 0px rgba(0,0,0,0.16);
    border-radius: 20px; /* Уголки */
}
.newgroup-button-dark:active {
    background:#2a2a2c;
    box-shadow:0px 3px 6px 0px rgba(0,0,0,0.16);
    -webkit-box-shadow:0px 3px 6px 0px rgba(0,0,0,0.16);
    -moz-box-shadow:0px 3px 6px 0px rgba(0,0,0,0.16);
    transition: 50ms ease all;
}
